 1px;\r\n  height: 100%;\r\n  width: 100%;\r\n  padding-top: 20px;\r\n  display: block;\r\n  text-align: center;\r\n  @media (min-width: 1000px) {\r\n    padding-top: 25px;\r\n  }\r\n}","$maincolor: purple;\r\n$mainBackgroundColor: #F3F3F3;\r\n",".location-container {\r\n  display: flex;\r\n  flex-direction: column;\r\n  align-items: center;\r\n  width: 100%;\r\n  @media (min-width: 1000px) {\r\n    width: 60%;\r\n    background-color: white;\r\n  }\r\n}\r\n\r\n.location-input {\r\n  max-width: 1000px;\r\n  width: 100%;\r\n  display: flex;\r\n  flex-direction: row;\r\n  justify-content: center;\r\n  align-items: center;\r\n  margin: 20px 0;\r\n  @media (min-width: 1000px) {\r\n    margin: 10px;\r\n  }\r\n}\r\n\r\n.location-input-form {\r\n  width: 80%;\r\n  display: flex;\r\n  flex-direction: row;\r\n  justify-content: space-between;\r\n  align-items: center;\r\n}\r\n\r\n.location-input-input {\r\n  height: 50px;\r\n  width: 80%;\r\n  font-size: 20px;\r\n  margin: 0 5px 0 0;\r\n  text-indent: 10px;\r\n  letter-spacing: 1px;\r\n  border: none;\r\n  border-left: 2px solid $maincolor;\r\n  &::placeholder {\r\n    color: grey;\r\n  }\r\n  &:active, &:focus {\r\n    outline: none;\r\n  }\r\n}\r\n\r\n%location-input-button {\r\n  width: 40px;\r\n  height: 40px;\r\n  background-color: purple;\r\n  border: none;\r\n  border-radius: 50%;\r\n  opacity: 0.9;\r\n  box-shadow: 0 2px 0 0 black;\r\n  position: relative;\r\n  top: 0;\r\n  margin-bottom: 2px;\r\n  margin-right: 5px;\r\n  &:hover {\r\n    opacity: 1;\r\n  }\r\n  &:active {\r\n    box-shadow: none;\r\n    top: 2px;\r\n    margin-bottom: 0;\r\n  }\r\n  &:active, &:focus {\r\n    outline: none;\r\n  }\r\n}\r\n\r\n.location-input-button-search {\r\n  @extend %location-input-button;\r\n  background-color: purple;\r\n  background-image: url(\"./../images/search.svg\");\r\n  background-size: cover;\r\n}\r\n\r\n.location-input-button-geolocation {\r\n  @extend %location-input-button;\r\n  background-color: purple;\r\n  backg